@import url("https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800");


* {
  box-sizing: border-box;
}

body {
  background: #2f8be8 !important;
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: "Fira Sans", sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6,
label,
span {
  font-weight: 500;
  font-family: "Fira Sans", sans-serif;
}

body,
html,
.App,
#root,
.auth-wrapper {
  width: 100%;
  height: 100%;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}


.auth-inner {
  width: 450px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all 0.3s;
}

.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
  background-color: #2f8be8;
  /* grid-template-columns: 20rem auto; */
}


.auth-wrapper .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}

.navbar-brand {
  color: #34557a;
}

.common-btn {
  width: 50px;
  /* set the width to the desired value */
}

th,
td {
  border: 1px solid #dee2e6;
}

@font-face {
  font-family: lobster;
  src: url(./Lobster-Regular.ttf);
}

.lobster{
  font-family: lobster;
}

@font-face {
  font-family: mogra;
  src: url(./Mogra-Regular.ttf);
}

.mogra{
  font-family: mogra;
}

.small-text {
  font-size: 12px;
  color: gray;
  text-align: center;
}

.custom-card {
  width: 100%;
}

@media (min-width: 768px) {
  .col-4 {
    flex: 0 0 33.33%;
    /* Adjust the percentage as needed */
    max-width: 33.33%;
    /* Adjust the percentage as needed */
  }
}


.btn-pass {
  background-color: green !important;
    color: black !important;
  width: 70px;
}

.btn-fail {
background-color: red !important;
  color: black !important;
  width:70px;
}